import Container from '@mui/material/Container';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import styled from 'styled-components';

import TodoList from 'components/todos/TodoList';
import TodoInput from 'components/todos/TodoInput';
import { useFetchTodoList } from 'hooks/todos/useFetchTodoList';
import CompletedTodoList from 'components/todos/CompletedTodoList';

const Todos = () => {
  useFetchTodoList();

  return (
    <SectionContainerWrap maxWidth={false}>
      <ContainerWrap maxWidth="lg">
        <h1>Todos</h1>

        <DndProvider backend={HTML5Backend}>
          <TodoInput />
          <TodoList />
          <CompletedTodoList />
        </DndProvider>
      </ContainerWrap>
    </SectionContainerWrap>
  );
};

const SectionContainerWrap = styled(Container)`
  /* background-color: #eee; */
  flex: 1 1 auto;
`;
const ContainerWrap = styled(Container)`
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
`;

export default Todos;
