import { Record } from 'immutable';
import { AnyAction } from 'redux';
import { createAction, handleActions } from 'redux-actions';

const prefix = 'users';

export const ActionTypes = {
  SET_USER_ME: `${prefix}/SET_USER_ME`,
};

export const ActionCtor = {
  setUserMe: createAction(ActionTypes.SET_USER_ME),
};

export function* UsersSaga() {}

interface IUser {
  id: number;
  name: string;
  email: string;
}
const UserRecord = Record<IUser>({
  id: 0,
  name: '',
  email: '',
});
export class UserState extends UserRecord {}

interface IUsersState {
  me: UserState;
}
const UsersStateRecord = Record<IUsersState>({
  me: new UserState(),
});
export class UsersState extends UsersStateRecord {}

const initState = new UsersState();
const UsersReducer = handleActions<UsersState, AnyAction>(
  {
    [ActionTypes.SET_USER_ME]: (state, action) => {
      return state.set('me', new UserState(action.payload.me));
    },
  },
  initState,
);

export default UsersReducer;
