import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import ClickAwayListener from '@mui/material/ClickAwayListener';
import Grow from '@mui/material/Grow';
import Paper from '@mui/material/Paper';
import Popper from '@mui/material/Popper';
import MenuItem from '@mui/material/MenuItem';
import MenuList from '@mui/material/MenuList';
import {
  KeyboardEvent,
  SyntheticEvent,
  useEffect,
  useRef,
  useState,
} from 'react';
import { Link, useHistory } from 'react-router-dom';
import styled from 'styled-components';

import { useFetchMe } from 'hooks/header/useFetchMe';

const {
  REACT_APP_CLIENT_ID = '',
  REACT_APP_REDIRECT_URI = '',
  REACT_APP_REDIRECT_URI_DEV = '',
} = process.env;

const REDIRECT_URI =
  process.env.NODE_ENV === 'production'
    ? REACT_APP_REDIRECT_URI
    : REACT_APP_REDIRECT_URI_DEV;

const Header = () => {
  const history = useHistory();
  const { isFetchedMe, username } = useFetchMe();
  const [open, setOpen] = useState(false);
  const anchorRef = useRef<HTMLButtonElement>(null);

  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };

  const handleClose = (event: Event | SyntheticEvent) => {
    if (
      anchorRef.current &&
      anchorRef.current.contains(event.target as HTMLElement)
    ) {
      return;
    }

    setOpen(false);
  };

  function handleListKeyDown(event: KeyboardEvent) {
    if (event.key === 'Tab') {
      event.preventDefault();
      setOpen(false);
    } else if (event.key === 'Escape') {
      setOpen(false);
    }
  }

  // 카카오 로그인 하기
  const onKakaoLogin = () => {
    const url = `https://kauth.kakao.com/oauth/authorize?client_id=${REACT_APP_CLIENT_ID}&redirect_uri=${REDIRECT_URI}&response_type=code`;
    window.location.href = url;
  };
  // 블로그 글쓰기 페이지 이동
  const goBlogRegisterPage = (event: Event | SyntheticEvent) => {
    history.push('/register');
    handleClose(event);
  };

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = useRef(open);
  useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current!.focus();
    }
    prevOpen.current = open;
  }, [open]);

  return (
    <HeaderWrap maxWidth={false}>
      <HeaderContainer maxWidth="lg">
        <TitleWrap>
          <Button
            variant="text"
            color="inherit"
            onClick={() => (window.location.href = '/dev-log/')}
          >
            <h1>정진근의 Dev Log</h1>
          </Button>
        </TitleWrap>

        <HeaderRightMenuWrap>
          <NavigationWrap>
            <Link to="/" color="inherit">
              <h3>Blogs</h3>
            </Link>
            <Link to="/todos" color="inherit">
              <h3>Todos</h3>
            </Link>
          </NavigationWrap>

          {isFetchedMe ? (
            <h4 style={{ marginLeft: 'auto', color: 'yellow' }}>
              <Button
                ref={anchorRef}
                id="composition-button"
                aria-controls={open ? 'composition-menu' : undefined}
                aria-expanded={open ? 'true' : undefined}
                aria-haspopup="true"
                color="inherit"
                onClick={handleToggle}
              >
                Hello, {username}!
              </Button>
              <Popper
                open={open}
                anchorEl={anchorRef.current}
                role={undefined}
                placement="bottom-start"
                transition
                disablePortal
              >
                {({ TransitionProps, placement }) => (
                  <Grow
                    {...TransitionProps}
                    style={{
                      transformOrigin:
                        placement === 'bottom-start'
                          ? 'left top'
                          : 'left bottom',
                    }}
                  >
                    <Paper>
                      <ClickAwayListener onClickAway={handleClose}>
                        <MenuList
                          autoFocusItem={open}
                          id="composition-menu"
                          aria-labelledby="composition-button"
                          onKeyDown={handleListKeyDown}
                        >
                          <MenuItem onClick={goBlogRegisterPage}>
                            내 블로그 작성
                          </MenuItem>
                          <MenuItem disabled={true} onClick={handleClose}>
                            내 정보
                          </MenuItem>
                          <MenuItem disabled={true} onClick={handleClose}>
                            로그아웃
                          </MenuItem>
                        </MenuList>
                      </ClickAwayListener>
                    </Paper>
                  </Grow>
                )}
              </Popper>
            </h4>
          ) : (
            <Button
              variant="contained"
              style={{
                backgroundColor: '#FEE500',
                color: '#000000',
                borderRadius: '12px',
                width: '120px',
                marginLeft: 'auto',
              }}
              onClick={onKakaoLogin}
            >
              카카오 로그인
            </Button>
          )}
        </HeaderRightMenuWrap>
      </HeaderContainer>
    </HeaderWrap>
  );
};

const HeaderWrap = styled(Container)`
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  background-color: #212529;

  color: white;
  padding: 0;
`;
const HeaderContainer = styled(Container)`
  display: flex;
  flex-flow: row nowrap;
  align-items: center;

  @media screen and (max-width: 768px) {
    flex-flow: column nowrap;
  }
`;
const TitleWrap = styled.div`
  flex: 0 0 auto;
`;
const HeaderRightMenuWrap = styled.div`
  margin-left: auto;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;

  @media screen and (max-width: 768px) {
    margin-left: unset;
    width: 100%;
  }
`;
const NavigationWrap = styled.div`
  flex: 0 0 auto;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin-right: 20px;

  & > * {
    margin: 0 10px;
    text-decoration: none;
    color: inherit;

    &:hover {
      text-decoration: underline;
    }
  }
`;

export default Header;
