import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import TextField from '@mui/material/TextField';
import Editor from '@toast-ui/editor';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import EmptyImage from 'assets/empty-image.png';

import CustomEditor from 'components/CustomEditor';
import { useCreateBlog } from 'hooks/todos/useCreateBlog';
import CircleProgress from 'components/CircleProgress';

const BlogRegisterTemplate = () => {
  const { onCreate, isLoading, isCreated } = useCreateBlog();
  const { push } = useHistory();
  const [title, setTitle] = useState('');
  const [editor, setEditor] = useState<Editor | null>(null);

  const onSave = () => {
    if (!title) {
      return;
    }
    if (!editor) {
      return;
    }
    onCreate(title, editor.getMarkdown(), EmptyImage);
  };
  const onChangeTitle = (e: React.ChangeEvent<HTMLInputElement>) => {
    setTitle(e.currentTarget.value);
  };
  const setEditorInstsance = (editorInstance: Editor) => {
    setEditor(editorInstance);
  };

  useEffect(() => {
    if (isCreated) {
      (async () => {
        await new Promise((resolve) => setTimeout(resolve, 200));
        push('/');
      })();
      return;
    }
  }, [isCreated, push]);

  return (
    <ContainerWrap maxWidth={false}>
      <div className="actionGroup">
        <Button
          className="saveBtn"
          variant="contained"
          disabled={isLoading}
          onClick={onSave}
        >
          {isLoading ? <CircleProgress /> : '저장'}
        </Button>
      </div>
      <div>
        <TextField
          placeholder="제목"
          fullWidth={true}
          margin="normal"
          value={title}
          onChange={onChangeTitle}
        />
      </div>
      <div className="editorWrap">
        <CustomEditor setEditorInstance={setEditorInstsance} />
      </div>
    </ContainerWrap>
  );
};

const ContainerWrap = styled(Container)`
  display: flex;
  flex-flow: column nowrap;

  .actionGroup {
    flex: 0 0 auto;

    .saveBtn {
      display: flex;
      margin-left: auto;
    }
  }
  .editorWrap {
    flex: 1 1 auto;
  }
`;

export default BlogRegisterTemplate;
