import { ApolloError, gql, useMutation } from '@apollo/client';
import { blogsApolloClient } from 'libs/graphql';

const CREATE_BLOG = gql`
  mutation create($title: String!, $body: String!, $thumbnail: String!) {
    create(
      createBlogData: { title: $title, body: $body, thumbnail: $thumbnail }
    ) {
      id
    }
  }
`;

interface IReturned {
  onCreate: (title: string, body: string, thumbnail: string) => void;
  isLoading: boolean;
  isCreated: boolean;
  error?: ApolloError;
}
export const useCreateBlog = (): IReturned => {
  const [createBlog, { loading, error, data }] = useMutation(CREATE_BLOG, {
    client: blogsApolloClient,
  });

  const onCreate = (title: string, body: string, thumbnail: string) => {
    createBlog({
      variables: {
        title,
        body,
        thumbnail,
      },
    });
  };

  return {
    onCreate,
    isLoading: loading,
    isCreated: !!data?.create?.id,
    error,
  };
};
