import { ApolloError, gql, useQuery } from '@apollo/client';
import { blogsApolloClient } from 'libs/graphql';

interface IBlog {
  id: number;
  title: string;
  body: string;
  thumbnail: string;
  createdAt: Date;
  updatedAt: Date;
  user: {
    id: number;
    name: string;
    email: string;
    image: string;
  };
}
interface IReturned {
  isLoading: boolean;
  data: {
    rows: IBlog[];
    count: number;
  };
  error?: ApolloError;
}
export const useFetchBlogList = (): IReturned => {
  const { loading, error, data } = useQuery(
    gql`
      query GetList($offset: Float!, $limit: Float!) {
        blogs(offset: $offset, limit: $limit) {
          id
          title
          body
          thumbnail
          createdAt
          updatedAt
          user {
            id
            name
            email
            image
          }
        }
        count
      }
    `,
    {
      client: blogsApolloClient,
      fetchPolicy: 'network-only',
      variables: {
        offset: 0,
        limit: 40,
      },
    },
  );

  return {
    isLoading: loading,
    data: {
      rows: data?.blogs || [],
      count: data?.count || 0,
    },
    error,
  };
};
